.table {
  max-width: 600px;
  margin: 0 auto;
  border-collapse: collapse;
  border: 1px solid black;
}

.table th,
.table td {
  padding: 1em;
  border: 1px solid black;
  text-align: left; /* or center, or right based on your preference */
}

.table thead {
  background-color: #f2f2f2; /* Optional: Adds a background color to the header */
}

.inventory-table {
  max-width: 1200px;
